import '../../../css/auth/register/sectionPassword.css'
import {Component} from "react";

class SectionPassword extends Component{
    constructor(props) {
        super(props);
        this.state={
            getPassword:'',
            checkPassword:'',
            warningPassword:'',
            warningPassword2:'',

            warning1:'',
            warning2:'',

            warning3:'',
            warning4:'',

            buttonClear:''
        }
    }
    getPassword=(e)=>{
        this.setState({
            warning1:'',
            warning2:'',
            warningPassword:'',
            warningPassword2:''
        })
        if(e.target.value===''){
            this.setState({
                warning1:'warning',
                warningPassword:'warning'
            })
            return;
        }
        if (!/^(?=.*[a-zA-Z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,32}$/.test(e.target.value)) {
            this.setState({
                warning2: 'warning',
                warningPassword:'warning'
            });
            return;
        }
        this.setState({
            getPassword:e.target.value
        })
    }

    checkPassword=(e)=>{
        this.setState({
            warning3:'',
            buttonClear:''
        })
        if(this.state.getPassword===''){
            return;
        }
        if(this.state.getPassword !== e.target.value){
            this.setState({
                warning3:'warning',
                warningPassword2:'warning'
            })
            return;
        }
        this.setState({
            checkPassword:e.target.value,
            buttonClear:'clear'
        })
    }
    getBtn2=()=>{
        this.props.inputPassword(this.state.getPassword);
        this.props.increaseLevel()
    }
    render(){
        if(this.props.getEmail === ''){
            this.props.renderBackup();
        }else{
            return(
                <div id="section-password">
                    <div className="cont_signt">
                        <h2 className="tit_gt">
                            <span>  </span>
                            <span>  .</span>
                        </h2>
                        <strong className="tit_tff"></strong>
                        <span className="txt_email"> {this.props.getEmail}</span>
                            <strong className="tit_tff"></strong>
                            <div className={`box_tff ${this.state.warningPassword}`} >
                                <input type="password" className="new_password" placeholder=" (8~32)" onBlur={this.getPassword}/>
                            </div>
                            <p className={`info_tff ${this.state.warning1}`}>  (//)</p>
                            <p className={`info_tff ${this.state.warning2}`}>//    .</p>
                            <div className={`box_tff ${this.state.warningPassword2}`}>
                                <input type="password" className="new_password_check" placeholder=" " onBlur={this.checkPassword} onChange={this.checkPassword}/>
                                <p className={`info_tff ${this.state.warning3}`}>     .</p>
                            </div>
                            <ul className="list_notice">
                                <li> 8~32  , ,   </li>
                                <li>        </li>
                                <li>       </li>
                            </ul>
                            <div className="confirm_btn">
                                <button className={`btn_g ${this.state.buttonClear}`} onClick={this.getBtn2}></button>
                            </div>
                    </div>
                </div>
            )
        }
    }
}

export default SectionPassword;